.lds {
    width: 80px;
    height: 80px;
    position: absolute;
    top: 30vh;
    left: calc(50% - 40px);
    div {
        display: inline-block;
        position: absolute;
        left: 8px;
        width: 16px;
        background: #1b1b1b;
        animation: lds 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
        &:nth-child(1) {
            left: 8px;
            animation-delay: -0.24s;
        }
        &:nth-child(2) {
            left: 32px;
            animation-delay: -0.12s;
        }
        &:nth-child(3) {
            left: 56px;
            animation-delay: 0;
        }
    }
}

@keyframes lds {
    0% {
        top: 8px;
        height: 64px;
    }
    50% {
        top: 24px;
        height: 32px;
    }
    100% {
        top: 24px;
        height: 32px;
    }
}
